.main-footer {
    padding: 2rem;
    display: grid;
    grid-template-columns: 1fr 2fr;
    gap: 1rem;
    margin-bottom: 3rem;

}

.footer-duo{
    display: flex;
    justify-content: space-between;

}



.mainFooter-data {
    padding-right: 3rem;
}

ul {
    list-style: none;
    text-align: left;
    padding-left: 0;

}

li {
    line-height: 2rem;
    font-size: 0.85rem;
    font-weight: 420;
    transition: all 0.3s;
    color: #000000c7;
}

li:hover {
    color: #ff4400be;
    transform: translateX(8%);
}

h5 {
    color: black;
    font-size: 1.2rem;
    font-weight: 550;
}

.footer-payment-thumb-wrap {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    row-gap: 1rem;
    align-items: center;
    margin-top: 1rem;

}

.ftr-description {
    font-size: 1rem;
    color: #0000006c;
    font-weight: 500;
}

.ftr-payments {
    margin-top: 1rem;
    font-size: 1.2rem;
    font-weight: 550;
    color: #000000a6;
}

.payment-single-item {
    width: 4.5rem;
    text-align: center;
    height: 2.5rem;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1.5px solid #8080807c;
    border-radius: 0.35rem;
}

.bottom-footer{
    padding: 1rem 2rem 2rem 2rem;
    margin: 0;
}

.copyright-content {
    display: grid;
    grid-template-columns: 1.5fr 1fr 0.8fr;
    justify-content: center;
    align-items: center;
}

.copyright-left-menu-wrap{
    display: flex;
}

.topbar-left-menu{
    display: flex;
    gap: 0.8rem;
}
.footer-menu-text{
    font-size: 1rem;
    font-weight: 420;
    
}



.footer-menu-link{
    font-size: 0.9rem;
    margin-right: 1rem;
    color: #000000c7;
    font-weight: 420;
    text-decoration: none;
    text-align: center;
}

.copyright-text{
    
    font-size: 0.9rem;
    color: #000000c7;
    font-weight: 420;

}

#button{
    padding: 0 3rem 0 0;
    cursor: pointer;
}

#button:hover{
    color:#ff4400be ;
}



