.BodyContainer {
    padding: 1rem 2rem; 
    
}

.BodyContainer h2 {
    font-size: 1.8rem;
    font-weight: 600;
    /* transform: translateY(100%); */
    transition: all 0.5s;
    margin-top: 2rem
}

@import url('https://fonts.googleapis.com/css2?family=Prompt:wght@400;500&display=swap');

.cat-type h3 {
    font-size: 2rem;
    font-weight: 700;
    color: #f5f5f575;
    position: absolute;
    z-index: 1;
    margin-left: -2rem;
    margin-top: 8rem;
    rotate: -90deg;
    font-family: 'Prompt', sans-serif;


}


.cat-type {
    overflow: hidden;
}

.CatsContainer {
    display: flex;
    justify-content: space-between;
    margin-top: 2rem;
    transition: all 1s;
    /* transform: translateX(100%); */

}


.cat-type img {
    width: 194px;
    height: 250px;
    position: relative;
    z-index: -1;
    border-radius: 1rem;
}

.cat-type-img:hover {
    scale: 1.5;
    transition: all 0.3s;
}

.DealsContainer {
    display: flex;
    overflow-x: scroll;
    gap: 2rem;
    margin-top: 2rem;
    position: relative;

}

.Deals-box {
    display: flex;
    font-size: 10px;
    flex-direction: column;
    width: 30rem;


}

.deals-img {
    width: 32em;
    scale: 0.8;
    transition: all 0.3s;
}

.deals-img:hover {
    scale: 1;
}


.product-div {
    background-color: #c9d9da46;
    padding: 1rem 2rem;
    border-radius: 1rem;
    overflow: hidden;
}

.wishlist-di {
    position: absolute;
    padding: 0.3rem;
    margin-top: 0.7em;
    margin-left: 33em;
}

.wishlist-di button {
    outline: none;
    border: none;
    background-color: white;
    border-radius: 50%;
    width: 2rem;
    height: 2rem;
}

.wishlist-di :hover {
    background-color: #ffc0cbd5;
    transition: all 0.3s;
}

.product-title {
    font-size: 1.8em;
    font-weight: 550;
    margin: 1rem 0;
}

.amount {
    font-size: 1.5em;
    font-weight: 400;
    margin-right: 1rem;
}

.addToCart-deals {
    background-color: inherit;
    padding: 0.5rem 1.5rem;
    border-radius: 2rem;
    font-size: 1rem;
    font-weight: 550;
    margin-top: 1rem;
    border-width: 1.5px;
    margin-bottom: 2rem;
}

.addToCart-deals:hover {
    background-color: #013d0e;
    color: white;
    transition: all 0.4s;
}

.amtcut {
    text-decoration: line-through;
}

.brands-head {
    margin-bottom: 2rem;
}

.BrandsContainer {
    display: grid;
    grid-template-columns: auto auto auto auto;
    row-gap: 1rem;
    column-gap: 0.5rem;
    justify-content: space-between;
    overflow: hidden;
    transition: all 0.6s;
    /* scale: 0.8; */

}

.brand img {
    height: 70px;
    width: 70px;
    border-radius: 50%;
}

.brand img:hover {
    scale: 1.1;
    transition: all 0.3s;
}

.brand {
    display: flex;
    align-items: center;
    padding: 0.5rem 1rem;
    width: 21rem;
    border-radius: 0.6rem;
    gap: 1rem;
    background-color: #c9d9da46;
    border: transparent 1px solid;
    transition: all 0.3s;

}

.brand:hover {
    border: black 1px solid;


}

.brand-info {
    padding-top: 0.5rem;
    width: 20rem;
    height: 4rem;
    align-items: center;

}


.brand-info h3 {
    font-size: 1rem;
    font-weight: 550;
}

.brand-info p {
    font-size: 0.8rem;
}

.OffersContainer {
    display: flex;
    justify-content: space-between;
}

.offers {
    margin-top: 1rem;
    border-radius: 1rem;
    width: 18rem;
    overflow: hidden;
    /* transform: translateY(20%); */
    transition: all 0.5s;
}

.offers-img {
    margin: 0;
    width: 18rem;
    overflow: hidden;
}

.offers-img img {
    transition: all 0.3s;
}

.offers-img img:hover {
    scale: 1.2;

}

.info--offers {
    margin: 2rem;
}

.offers-save {
    font-size: 1.5rem;
    font-weight: 550;
}

.offers-off {
    font-size: 2.5rem;
    font-weight: 660;

}

.offer-description {
    font-size: 1rem;
}

.off--color--yellow {
    color: #ebb400;
}

.furni--offer {
    background-color: #ffd95c54;
}

.books--offer {
    background-color: #ff000027;
}

.off--color--red {
    color: #6d1313;
}

.fashion--offer {
    background-color: #99513234;
}

.off--color--pink {
    color: #a06539;
}

.off--color--green {
    color: #003d29;
}

.bags--offer {
    background-color: #d2f7ec;
}



.promo-btn {
    border: 1.2px solid white;
    background-color: inherit;
    
}

.promotionMain {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    z-index: -1;
    background-image: url(https://uploads-ssl.webflow.com/63e857e…/63e8c4e…_biscount%20banner-min.png);
    background-position: 50% 50%;
    background-size: cover;
    background-repeat: no-repeat;
    
    /* will-change: transform;
    transform: translate3d(0px, 0px, 0px) scale3d(1, 1, 1) rotateX(0deg) rotateY(0deg) rotateZ(0deg) skew(0deg, 0deg);
    transform-style: preserve-3d; */

}
.d3promo{
    position: relative;
    z-index: 0;
    padding-top: 95px;
    padding-bottom: 95px;
    display: flex;
    justify-content: end;
    padding-right: 4rem;
    margin: 2rem -2rem 0 -2rem
}


.promo-info {
    background-color: #003d29;
    width: 35rem;
    height: 25rem;
    color: white;
    margin-left: 48rem;
    padding: 4rem 3rem;
    

}
.head-promo{
    width: 30rem
}


@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600&family=Prompt:wght@400;500&display=swap');


.promo-info h1 {
    font-weight: 550;
    font-family: 'Inter', sans-serif;
    font-family: 'Prompt', sans-serif;
}

.promo-info p{
    margin-top: 1rem;
    font-size: 1.2rem;
}

.td-nav{
    border-radius: 2rem;
    padding: 0.6rem 2rem;
    background-color: inherit;
    border: 1.2px solid black;
    color: black;
    font-size: 0.8rem;
    font-weight: 550;
    width: 7rem;
    text-decoration: none;
    text-align: center;
    transition: all 0.3s;
}

.todays-deals-nav{
    margin-top: 2rem;
    display: flex;
    gap: 1rem;
}

.gadgets .Deals-box{
    width: 17.5rem;
    font-size: 7px;
}

.gadgets{
    margin-top: 2rem;
    display: grid;
    grid-template-columns: auto auto auto auto;
    justify-content: space-between;
}

.promotions{
    background-color: #ffe6cc;
    padding: 3.5rem 0 0 0;
    position: relative;
    margin: 2rem -2rem 2rem -2rem;
}


.promo-mini-info-data{
    padding-right: 10rem;
}

.promo-mini-info{
    display: flex;
    justify-content: space-between;
    align-items: center;
}





.promo-mini-info-data button{
    background-color: #003d29;
    color: white;

}
.trendingContainer{
    display: flex;
    justify-content: space-between;
}

.trending-products{
    display: flex;
    flex-direction: column;
    gap: 1rem;
    width: 39rem;
   margin-top: 2rem;
    border-radius: 2rem;
    padding-bottom: 2rem;
}

.icon-trend{
    width: 100px;
    position: absolute;
    border-radius: 50%;
    margin-top: 18rem;
    border: 4px solid white;
    margin-left: 30rem;
}

.trend-info{
    margin-left: 1rem;
}

.trend-info button{
    background-color: rgba(0, 0, 51, 0.637);
}

.trend-info h4{
    font-weight: 650;
}

.img-trend{
    position: relative;
   
    width: 100%;
    transition: all 0.3s;

}

.img-trend:hover{
    scale: 1.2 ;
    margin-top: 2rem;
    overflow: hidden;
}

.ikea-trend{
background-color: #fffd7fea;
}

.main-trend-img{
    width: 39rem;
    height: 20rem;
    overflow: hidden;
    border-top-left-radius: 2rem;
    border-top-right-radius: 2rem;

}

.jahapana-trend{
    background-color: #ffc0cbaf;
}

.services{
    margin-bottom: 5rem;
}

.servicesContainer{
    display: flex;
    justify-content: space-between;
    margin-top: 2rem;
   
    
}

.service{
    width: 25rem;
    overflow: hidden;
    padding-top: 1rem;
    background-color: #c9d9da46;
    border-radius: 1rem;
    
}

.service-img{

    overflow: hidden;
    transition: all 0.3s;
    border-bottom-left-radius: 1rem;
    border-bottom-right-radius: 1rem;

}

.service-info{
    padding: 2rem;
    height: 11rem;
}

.service-info .h7{
    font-size: 1rem;
    font-weight: 500;
}

.service-img img:hover{
    scale: 1.1;
}

.service-img img{
    transition: all 0.3s;
}




