.HeadContainer {
    position: relative;
    z-index: -1;
    overflow-x: hidden;
}

.bgImg {
    filter: contrast(0.8);
    opacity: 0.8;
    width: 100%;
    height: 40rem;
    position: relative;
    z-index: -1;
}

.Headings {
    position: absolute;
    z-index: 1;
    top: 12rem;
    left: 5rem;
}

.Head2{
    padding-left: 5rem;
}

.statement-head{
    font-size: 1.4rem;
    font-weight: 400;
    width: 52%;
    color: #000000bb;
    margin-top: 2rem;
}

.LearnMore-btn{
    background-color: #013d0e;
    color: white;
    font-size: 1.2rem;
    font-weight: 400;
    border-radius: 2rem;
    outline: none;
    border: none;
    padding: 1rem 2rem;
    margin-top: 2rem;
    transition: all 0.3s;

}

.LearnMore-btn:hover{
    background-color: black;
    

}

.banner{
    color: #013d0e;
    font-weight: 600;
    font-size: 4rem;
    transform: translateX(-110%);  /*trasnlate*/
    transition: all 0.3s;
}

.headblock{
    overflow-x: hidden;
}

.bannery{
    transform: translateY(150%);   /*trasnlate*/
    transition: all 0.3s;
}
.h1-head2{
    transition-delay: 1s;
}

.learnMore, .slogy {
    height: 6rem;
    overflow: hidden;

}


.Stairs-anime{
    position: absolute;
    top: -0.4rem;
    transform: translateX(90%);   /*trasnlate*/
    transition: all 1s;
    overflow: hidden;
    right: -70px;
    
}
.stairs{
    overflow: hidden;
}

.items {
    position: absolute;
    
   

}

.items img{
    transition: 1s all;
    transform: translateY(-50%); 
}


.fashion{
    top: 6rem;
    right: 4rem;
}
.travel{
    right: 22rem;
    top:10rem ;
}
.snacks{
    top: 20rem;
    right: 32rem;
}
.electronics{
    top: 18rem;
    right: 8rem;
}



