.Header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 25px;
}

.NavBar{
    display: flex;
    font-size: 1rem;

}

.Navs{
    padding: 5px 10px;
    text-decoration: none;
    color: black;
    align-items: center;
    width: 10rem;
    text-align: center;

}

.Navs:hover{
    color: grey;
    cursor: pointer;
}
.Menu{
    height: 30px;
    width: 30px;
    padding: 2px;
    margin-right: 10px;
    display: none;
}
.Menu img{
    width: 100%;
}

.account{
    display: flex;
    gap: 10px;
    align-items: center;
    

}

.Nav2{
    display: flex;
    justify-content: flex-end;
    margin-left: 4rem;
}

.CartBtn{
    background-color: inherit;
    border: none;
    width: 6rem;

}

.searchNav input{
    line-height: 1.4;
    color: #333333;
    background-color: #ffffff;
    border: 1px solid #cccccc;
    position: relative;
    z-index: 1;
    width: 100%;
    height: 2rem;
    text-align: left;
    padding-right: 5rem;
    padding-left: 2rem;
    border-radius: 10rem

}

.searchNav{
    display: flex;
    align-items: center;
    position: relative;
}

.searchNav button{
    border: 0;
    background-color: transparent;
    position: absolute;
    z-index: 2;
    right: 0.5rem;
    cursor: pointer;
    
}

.searchNav svg{
    color: #333333;
}
#searchInput:focus{
    outline: #025a15 1.5px solid
 }

#searchInput:hover{
    cursor: text;
}






